body {
    /* Custom */
    --button-height: 3.5rem;
    --button-radius: calc(var(--button-height) / 2);
    --app-height: var(--tg-viewport-height);

    /* Colors */
    --font-bright: white;
    --font-dim: #606060;
    --font-semi-dim: #808080;
    --font-blue: #207BF9;
    --bg-black: black;
    --bg-gray: #212121;
    --progress-bar-button-bg: #1E1E22;
    --rate-bar-color: #5E5E5E;

    --color-blue: #207BF9;
    --color-red: #FC302C;
    --color-orange: #FB7429;
    --color-yellow: #F6ED1E;
    --color-green: #26EF5E;

    --paywall-plan-bg: #131313;

    --system-font: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif, "Inter";

    /* CommonOld */
    margin: 0;
    background-color: #fff;
    font-family: monospace;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-tap-highlight-color: transparent;
    touch-action: none !important;
}

hr {
    height: 1px;
    width: 100%;
    border: none;
    color: #1e1e1e;
    background-color: #aaaaaa;
}

.entry-text-bold {
    font-weight: 600;
}

.manual-select-list {
    display: flex;
    flex-direction: column;
    gap: 0.2rem;
}

.manual-selector {
    user-select: none;
}