.mini-app-main-view {
    text-align: center;
    display: grid;
    grid-template-rows: 0 1fr 4rem;
    margin: 0;
    overflow: hidden;
    background: #fff;
}

.main {
    margin: auto;
    width: 95%;
}

html {
    font-size: 100%;
}

.entry {
    display: flex;
    flex-direction: column;
    background-color: #eee;
    margin: 1rem;
    padding: 1rem;
    border-radius: 1rem;
}

.entry-controls {
    display: flex;
    flex-direction: row;
    gap: 1rem;
}

.category {
    font-weight: 600;
    color: white;
    padding: 0.2rem 0.5rem;
    border-radius: 0.5rem;
    width: fit-content;
}

.category-list {
    display: flex;
    gap: 0.5rem
}

.entry-closed {
    display: flex;
    flex-direction: column;
    background-color: #ddd;
    margin: 1rem;
    padding: 1rem;
    border-radius: 1rem;
}