.log {
    display: flex;
    flex-direction: column;
    gap: 0.5rem
}

.log-entry {
    display: flex;
    flex-direction: row;
    gap: 0.2rem
}

.callstack {
    display: flex;
    flex-direction: column;
    gap: 0.1rem
}

#drop-zone {
    width: 50rem;
    height: 20rem;
    margin: auto;
    margin-top: 5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0.5rem;
    border: 1px solid #cccccc;
    background-color: #eeeeee;
}